import { Container, Row, Col, Tab, Nav } from "react-bootstrap";
import { ProjectCard } from "./ProjectCard";
import cicdimg from "../assets/img/cicdimg.png";
import flightmanagementimg from "../assets/img/flightmanagementimg.png";
import disastervictimimg from "../assets/img/disastervictimimg.png";
import movierecommenderimg from "../assets/img/movierecommenderimg.png";
import carbonfootprintimg from "../assets/img/carbonfootprintimg.png";
import virtualstudyroomimg from "../assets/img/virtualstudyroomimg.png";
import meetingsummarizerimg from "../assets/img/meetingsummarizerimg.png";
import githubIcon from "../assets/img/nav-icon2.svg";
import colorSharp2 from "../assets/img/color-sharp2.png";
import 'animate.css';
import TrackVisibility from 'react-on-screen';

export const Projects = () => {

  const completedProjects = [
    {
      title: "CI/CD Pipeline",
      description: "Design & Development of a CI/CD pipeline project.",
      imgUrl: cicdimg,
      githubUrl: "https://github.com/rajdeeepdas/ensf400-lab8-kubernetes-2",
    },
    {
      title: "Flight Management System",
      description: "Development of a flight management system.",
      imgUrl: flightmanagementimg,
      githubUrl: "https://github.com/rajdeeepdas/-Flight-Management-Program",
    },
    {
      title: "Disaster Victim Assistance",
      description: "A system to assist disaster victims effectively.",
      imgUrl: disastervictimimg,
      githubUrl: "https://github.com/username/disaster-victim",
    },
    {
      title: "Movie Recommender",
      description: "A recommendation system for movies.",
      imgUrl: movierecommenderimg,
      githubUrl: "https://github.com/rajdeeepdas/movie-recommender",
    },
  ];

  const inProgressProjects = [
    {
      title: "Carbon Footprint Calculator",
      description: "Developing a calculator for carbon footprint estimation.",
      imgUrl: carbonfootprintimg,
    },
    {
      title: "Virtual Study Room",
      description: "Creating an interactive virtual study environment.",
      imgUrl: virtualstudyroomimg,
    },
    {
      title: "Meeting Summarizer",
      description: "Summarizing meeting notes automatically.",
      imgUrl: meetingsummarizerimg,
    },
  ];

  return (
    <section className="project" id="projects">
      <Container>
        <Row>
          <Col size={15}>
            <TrackVisibility>
              {({ isVisible }) =>
                <div className={isVisible ? "animate__animated animate__fadeIn" : ""}>
                  <h2>Projects</h2>
                  <p>showcase.</p>
                  <Tab.Container id="projects-tabs" defaultActiveKey="completed">
                    <Nav variant="pills" className="nav-pills mb-5 justify-content-center align-items-center" id="pills-tab">
                      <Nav.Item>
                        <Nav.Link eventKey="completed">Completed Projects</Nav.Link>
                      </Nav.Item>
                      <Nav.Item>
                        <Nav.Link eventKey="inProgress">Projects On Build</Nav.Link>
                      </Nav.Item>
                    </Nav>
                    <Tab.Content id="slideInUp" className={isVisible ? "animate__animated animate__slideInUp" : ""}>
                      <Tab.Pane eventKey="completed">
                        <Row>
                          {
                            completedProjects.map((project, index) => {
                              return (
                                <ProjectCard
                                  key={index}
                                  {...project}
                                  githubIcon={githubIcon}
                                />
                              )
                            })
                          }
                        </Row>
                      </Tab.Pane>
                      <Tab.Pane eventKey="inProgress">
                        <Row>
                          {
                            inProgressProjects.map((project, index) => {
                              return (
                                <ProjectCard
                                  key={index}
                                  {...project}
                                />
                              )
                            })
                          }
                        </Row>
                      </Tab.Pane>
                    </Tab.Content>
                  </Tab.Container>
                </div>
              }
            </TrackVisibility>
          </Col>
        </Row>
      </Container>
      <img className="background-image-right" src={colorSharp2} alt="background"/>
    </section>
  );
}
