import { Container, Row, Col } from "react-bootstrap";
import logo from "../assets/img/ucalgarylogo.png";
import navIcon1 from "../assets/img/nav-icon1.svg";
import navIcon2 from "../assets/img/nav-icon2.svg";
import navIcon3 from "../assets/img/nav-icon3.svg";

export const Footer = () => {
  return (
    <footer className="footer">
      <Container>
        <Row className="align-items-center">
          <Col size={12} sm={6}>
          <img src={logo} alt="Logo" style={{ width: '150px', height: 'auto' }} />
          </Col>
          <Col size={15} sm={6} className="text-center text-sm-end">
          <div className="social-icon">
  <a href="https://www.linkedin.com/in/rajdeep-das-432a58252" target="_blank" rel="noopener noreferrer">
    <img src={navIcon1} alt="LinkedIn" />
  </a>
  <a href="https://github.com/rajdeeepdas" target="_blank" rel="noopener noreferrer">
    <img src={navIcon2} alt="GitHub" />
  </a>
  <a href="https://discordapp.com/users/1159690515181223957" target="_blank" rel="noopener noreferrer">
    <img src={navIcon3} alt="Discord" />
  </a>
</div>
            <p>Copyright 2025. All Rights Reserved</p>
          </Col>
        </Row>
      </Container>
    </footer>
  )
}