import React from "react";
import Carousel from "react-multi-carousel";
import "react-multi-carousel/lib/styles.css";

// Keep arrow1, arrow2, and colorSharp if you still want to use them in your layout
import arrow1 from "../assets/img/arrow1.svg";
import arrow2 from "../assets/img/arrow2.svg";
import colorSharp from "../assets/img/color-sharp.png";

// Import your skill images
import awsimg from "../assets/img/awsimg.svg";
import cimg from "../assets/img/Cimg.svg";
import cplusplusimg from "../assets/img/C++img.svg";
import cssimg from "../assets/img/cssimg.svg";
import dockerimg from "../assets/img/dockerimg.svg";
import htmlimg from "../assets/img/htmlimg.svg";
import humanlanguageimg from "../assets/img/humanlanguageimg.svg";
import javaimg from "../assets/img/javaimg.svg";
import javascriptimg from "../assets/img/javascriptimg.svg";
import jenkinsimg from "../assets/img/jenkinsimg.svg";
import kubernetesimg from "../assets/img/kubernetesimg.svg";
import linuximg from "../assets/img/linuximg.svg";
import microsoftofficeimg from "../assets/img/microsoftofficeimg.svg";
import nodejsimg from "../assets/img/Node.jsimg.svg";
import pythonimg from "../assets/img/Pythonimg.svg";
import reactimg from "../assets/img/reactimg.svg";
import sqlimg from "../assets/img/SQLimg.svg";
import tensorflowimg from "../assets/img/tensorflowimg.svg";

export const Skills = () => {
  // Carousel breakpoints
  const responsive = {
    superLargeDesktop: {
      breakpoint: { max: 4000, min: 3000 },
      items: 5,
    },
    desktop: {
      breakpoint: { max: 3000, min: 1024 },
      items: 3,
    },
    tablet: {
      breakpoint: { max: 1024, min: 464 },
      items: 2,
    },
    mobile: {
      breakpoint: { max: 464, min: 0 },
      items: 1,
    },
  };

  // Array of skill objects (name + image)
  const skillsData = [
    { name: "AWS", image: awsimg },
    { name: "C", image: cimg },
    { name: "C++", image: cplusplusimg },
    { name: "CSS", image: cssimg },
    { name: "Docker", image: dockerimg },
    { name: "HTML", image: htmlimg },
    { name: "6 Human Languages", image: humanlanguageimg },
    { name: "Java", image: javaimg },
    { name: "JavaScript", image: javascriptimg },
    { name: "Jenkins", image: jenkinsimg },
    { name: "Kubernetes", image: kubernetesimg },
    { name: "Linux", image: linuximg },
    { name: "Microsoft Office", image: microsoftofficeimg },
    { name: "Node.js", image: nodejsimg },
    { name: "Python", image: pythonimg },
    { name: "React", image: reactimg },
    { name: "SQL", image: sqlimg },
    { name: "TensorFlow", image: tensorflowimg },
  ];

  return (
    <section className="skill" id="skills">
      <div className="container">
        <div className="row">
          <div className="col-12">
            <div className="skill-bx wow zoomIn">
              <h2>Skills</h2>
              <p>
                Various Languages accuired through exploration of academic content and personal curiosity.
                <br />
                All of the claims regarding the profiency of these languages are backed by appropiate Projects.
              </p>
              <Carousel
                responsive={responsive}
                infinite={true}
                className="owl-carousel owl-theme skill-slider"
              >
                {skillsData.map((skill, index) => (
                  <div className="item" key={index}>
                    <img src={skill.image} alt={skill.name} />
                    <h5>{skill.name}</h5>
                  </div>
                ))}
              </Carousel>
            </div>
          </div>
        </div>
      </div>
      {/* Optional background image at the bottom-left corner */}
      <img className="background-image-left" src={colorSharp} alt="Background" />
    </section>
  );
};
